import { createWebHistory, createRouter } from "vue-router";
import { gsap, Expo } from 'gsap';

const tl = gsap.timeline();

const waveReset = (wave, tl) => {
  wave.style.opacity = 0;
  tl.to(wave, 0.75, { y: "100%", ease: Expo.easeInOut });
};

const waveShow = (wave, tl, next) => {
  wave.style.opacity = 1;
  tl.to(wave, 0.75, { y: 0, ease: Expo.easeInOut });
  tl.to(wave, 0.75, {
    y: "-110%",
    ease: Expo.easeInOut,
    onComplete: () => {
      waveReset(wave, tl);
    },
  });
};

const routes = [
  {
    path: "/",
    name: "Main",
    component: () => import("@/views/Main.vue")
  },
  {
    path: "/about",
    name: "About",
    component: () => import("@/views/About.vue")
  },
  {
    path: "/Work",
    name: "Work",
    component: () => import("@/views/Work.vue")
  },
  {
    path: "/WorkView:id",
    name: "WorkView",
    component: () => import("@/components/Layouts/Views/WorkView.vue"),
    meta: { requiresAuth: true }
  },
  {
    path: "/Contact",
    name: "Contact",
    component: () => import("@/views/Contact.vue")
  },
  {
    path: "/Blog",
    name: "Blog",
    component: () => import("@/views/Blog.vue")
  },
  {
    path: "/BlogView",
    name: "BlogView",
    component: () => import("@/components/Layouts/Views/BlogView.vue")
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { top: 0 };
    } 
  },
});

router.beforeEach((to, from, next) => {
  const header = document.getElementsByTagName("header")[0];
  const wave = document.getElementById("wave");
  if (!header.classList.contains('open')) {
    waveShow(wave, tl );
    setTimeout(() => {
      next();
    }, 1200);
  }
})
 
export default router; 